<template>
  <section id="work">
    <div class="wave-wrapper wave-animation top">
      <div class="wave-wrapper-inner bg-top">
        <div class="wave wave-top" />
      </div>
      <div class="wave-wrapper-inner bg-middle">
        <div class="wave wave-middle" />
      </div>
      <div class="wave-wrapper-inner bg-bottom">
        <div class="wave wave-bottom" />
      </div>
    </div>
    <div class="wave-section3-content">
      <img class="logo" src="../assets/about.png" alt="selfpick" />
      <div class="content">
        <img src="../assets/s3-jack.png" />
        <div>
          <p>SELFPICK 是一間以「沈浸式娛樂」為命題的新創新媒體公司，「沈浸式娛樂」從虛實整合做起，
          延伸到「視覺」「聽覺」「嗅覺」「味覺」的SELFPICK專屬五感體驗，開創其周邊商品以及概念，
          最終匯聚成一個具有品牌價值的意念「做自己的選擇，走對的路」。</p>
        </div>
      </div>
    </div>
    <div class="wave-wrapper wave-animation">
      <div class="wave-wrapper-inner bg-top">
        <div class="wave wave-top" />
      </div>
      <div class="wave-wrapper-inner bg-middle">
        <div class="wave wave-middle" />
      </div>
      <div class="wave-wrapper-inner bg-bottom">
        <div class="wave wave-bottom" />
      </div>
    </div>
  </section>
</template>
<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Introduction',
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('#work .content', {
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style scoped lang="scss">
  @keyframes move-wave {
    0% {
      transform: translateX(0) translateZ(0) scaleY(1);
    }
    50% {
      transform: translateX(-25%) translateZ(0) scaleY(0.55);
    }
    100% {
      transform: translateX(-50%) translateZ(0) scaleY(1);
    }
  }

  @keyframes move-wave-mobile {
    100%  {
      background-position: -1920px bottom;
    }
  }

  section {
    @media screen and (min-width: 768px){
      min-height: auto;
      min-height: 768px;
    }
  }

  #work {
    position: relative;
  }

  .wave-section3-content {
    height: 100vh;
    max-height: 650px;
    position: relative;
    z-index: 20;

    @media screen and (min-width: 768px){
      max-height: 750px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .logo {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      z-index: 1;
    }

    .content {
      position: relative;
      padding: 25% 0;
      z-index: 2;

      @media screen and (min-width: 768px){
        max-width: 90%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
      }

      img {
        max-width: 60%;

        @media screen and (min-width: 768px){
          max-width: 35%;
          margin-left: 10%;
        }
      }

      > div {
        max-width: 90%;
        margin: auto;

        @media screen and (min-width: 768px){
          max-width: 55%;
          margin-left: 10%;
        }
      }

      p {
        color: #fff;
        font-size: 18px;
        text-align: left;
        line-height: 2em;
      }
    }
  }

  .wave-wrapper {
      overflow: hidden;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      margin: auto;
  }

  .top {
    .wave-wrapper-inner {
      transform: rotate(180deg);
      background-image: linear-gradient(to bottom,
      transparent 0%,
      transparent 20%,
      rgb(5, 88 ,192) 20%,
      rgb(5, 88 ,192) 80%
      );
    }
  }

  .wave-wrapper-inner {
    position: absolute;
    width: 100%;
    overflow: hidden;
    height: 100%;
    bottom: 0px;
    background-image: linear-gradient(
      to top,
      rgb(5, 88 ,192) 20%,
      rgb(5, 88 ,192) 80%)
  }

  .top {
    .bg-top {
      z-index: 6;
      opacity: 0.5;
    }
    .bg-middle {
      z-index: 5;
      opacity: 0.75;
    }
    .bg-bottom {
      z-index: 4;
    }
  }
  .bg-top {
    z-index: 3;
    opacity: 0.5;
  }
  .bg-middle {
    z-index: 2;
    opacity: 0.75;
  }
  .bg-bottom {
    z-index: 1;
  }
  .wave-top {
    background: url('../assets/wave-top.png');
    background-size: 960px 100px;

    @media screen and (min-width: 768px){
      background-size: 50% 100px;
    }
  }
  .wave-animation .wave-top {
    -webkit-animation:100s move-wave-mobile infinite linear;
    animation:100s move-wave-mobile infinite linear;
    will-change: animation;

    @media screen and (min-width: 768px){
      animation: move-wave 3s;
      -webkit-animation: move-wave 3s;
      -webkit-animation-delay: 1s;
      animation-delay: 1s;
    }
  }
  .wave-middle {
    background: url('../assets/wave-mid.png');
    background-size: 960px 120px;

    @media screen and (min-width: 768px){
      background-size: 50% 120px;
    }
  }
  .wave-animation .wave-middle {
    -webkit-animation:200s move-wave-mobile infinite linear;
    animation:200s move-wave-mobile infinite linear;
    will-change: animation;

    @media screen and (min-width: 768px){
      -webkit-animation: move-wave 10s linear infinite;
      animation: move-wave 10s linear infinite;
    }
  }
  .wave-bottom {
    background: url('../assets/wave-bot.png');
    background-size: 960px 100px;

    @media screen and (min-width: 768px) {
      background-size: 50% 100px;
    }
  }
  .wave-animation .wave-bottom {
    -webkit-animation:300s move-wave-mobile infinite linear;
    animation:300s move-wave-mobile infinite linear;
    will-change: animation;

    @media screen and (min-width: 768px){
      -webkit-animation: move-wave 15s linear infinite;
      animation: move-wave 15s linear infinite;
    }
  }

  .wave {
      position: absolute;
      left: 0;
      width: 200%;
      height: 100%;
      background-repeat: repeat no-repeat;
      background-position: 0 bottom;
      transform-origin: center bottom;
  }
</style>



