<template>
  <div class="sidebar">
    <a class="sidebar-1" href="https://selftoken.co" target="_blank">
      <img src="../assets/side-1.png" />
    </a>
    <a class="sidebar-2" href="https://jimlin94.github.io/2">
      <img src="../assets/side-2.png" />
    </a>
    <a class="sidebar-3" href="https://www.facebook.com/selfpick/" target="_blank">
      <img src="../assets/side-3.png" />
    </a>
    <a class="sidebar-4" href="https://www.instagram.com/selfpick/" target="_blank">
      <img src="../assets/side-4.png" />
    </a>
  </div>
</template>

<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Sidebar',
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.sidebar', {
      origin: 'bottom',
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style lang="scss" scoped>
  @import "../hamburgers.css";

  .sidebar {
    position: fixed;
    top: 0;
    right: 5%;
    display: flex;
    justify-content: space-around;
    cursor: pointer;
    z-index: 106;

    @media screen and (min-width: 768px) {
      width: auto;
      top: auto;
      bottom: 20%;
      right: auto;
      left: 5%;
      display: block;
    }

    a {
      display: block;
      margin: 20px 10px;
      flex-direction: column;
      // cursor: pointer;
      z-index: 10;

      img {
        width: 25px;
        -webkit-filter: drop-shadow(10px 10px 10px rgba(0,0,0,0.9));
        filter: drop-shadow(10px 10px 10px rgba(0,0,0,0.9));

        @media screen and (min-width: 768px) {
          width: auto;
        }
      }
    }
  }
</style>



