<template>
  <section id="member">
    <img class="logo" src="../assets/members.png" alt="selfpick" />
    <div class="profiles">
      <div
        class="profile"
        v-for="profile in profiles"
        :key="profile.id"
      >
        <div class="content">
        <img v-bind:src="require(`@/assets/${profile.imgUrl}`)" :alt="profile.englishDesc" />
          <div class="desc">
            <div class="chinese-desc">
              <span class="title">{{ profile.chineseTitle }}</span>
              <span class="name">{{ profile.chineseName }}</span>
            </div>
            <div class="english-desc">
              <span class="title">{{ profile.englishTitle }}</span>
              <span class="name">{{ profile.englishName }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Members',
  data() {
    return {
      profiles: [
        {
          id: 1,
          imgUrl: '01-jack.png',
          chineseTitle: '導演',
          chineseName: '徐嘉凱',
          englishTitle: 'DIRECTOR',
          englishName: 'JACK HSU',
        },
        {
          id: 2,
          imgUrl: '02-vicky.png',
          chineseTitle: '製片',
          chineseName: '吳佳如',
          englishTitle: 'PRODUCER',
          englishName: 'VICKY WU',
        },
        {
          id: 3,
          imgUrl: '03-zying.png',
          chineseTitle: '藝術總監',
          chineseName: '何姿瑩',
          englishTitle: 'ARTIST DIRECTOR',
          englishName: 'ZYING HO',
        },
        {
          id: 4,
          imgUrl: '04-venk.png',
          chineseTitle: '音樂總監',
          chineseName: '楊子樸',
          englishTitle: 'MUSIC DIRECTOR',
          englishName: 'VENK YANG',
        },
        {
          id: 5,
          imgUrl: '05-eddie.png',
          chineseTitle: '行銷經理',
          chineseName: '游子毅',
          englishTitle: 'MARKETING MANAGER',
          englishName: 'EDDIE YU',
        },
        {
          id: 6,
          imgUrl: '06-char.png',
          chineseTitle: '專案經理',
          chineseName: '蕭宇伶',
          englishTitle: 'PROJECT MANAGER',
          englishName: 'CHARMING HSIAO',
        },
        {
          id: 7,
          imgUrl: '07-eris.png',
          chineseTitle: '內容商務開發經理',
          chineseName: '李宛庭',
          englishTitle: 'CONTENT BUSINESS MANAGER',
          englishName: 'ERIS LEE',
        },
        {
          id: 8,
          imgUrl: '08-howwei.png',
          chineseTitle: '銷售助理',
          chineseName: '吳孟倫',
          englishTitle: 'PROJECT ASISTANCE',
          englishName: 'HOWWEI HCHEN',
        },
      ],
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.profiles .profile', {
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style scoped lang="scss">
  section {
    height: 100vh;
    max-height: none;
    height: auto;

    @media screen and (min-width: 1024px) {
      max-width: calc(180px * 4 + 80px);
      height: 100vh;
      max-height: 750px;
      margin: auto;
      padding: 20px 0;
    }
  }

  #member {
    position: relative;
  }

  .logo {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    padding: 5%;
    z-index: 1;
  }

  .profiles {
    position: relative;
    width: 100%;
    margin: 20px auto;
    display: flex;
    align-content: space-around;
    align-items: center;
    flex-flow: wrap;
    align-items: start;
    z-index: 5;

    @media screen and (min-width: 1024px) {
      max-width: calc(180px * 4 + 80px);
      height: 100vh;
      max-height: 750px;
      margin: auto;
      padding: 20px 0;
    }
  }

  .profile {
    width: 100%;
    margin: 10px;

    @media screen and (min-width: 768px) {
      width: 23%;
      max-width: 180px;
    }

    .content {
      width: 100%;
      display: flex;
      align-content: center;
      align-items: center;
      justify-content: center;
      flex-direction: row;

      @media screen and (min-width: 768px) {
        display: block;
      }

      img {
        max-width: 40%;
        display: inline-block;

        @media screen and (min-width: 768px) {
          max-width: 100%;
        }
      }

      .desc {
        width: 50%;
        margin-top: 20px;
        padding-left: 10px;
        display: inline-block;
        font-size: 0.8em;
        text-align: left;

        @media screen and (min-width: 768px) {
          width: 100%;
          padding-left: 0;
          font-size: .9em;
          text-align: center;
        }

        .chinese-desc {
          > span {
            color: #001940;
          }
        }

        .english-desc {
          > span {
            display: block;
            color: #e0503f;

            @media screen and (min-width: 768px) {
              display: flex;
            }
          }
        }

        .english-desc, .chinese-desc {

          @media screen and (min-width: 768px) {
            display: flex;
          }

          span {
            width: 100%;

            @media screen and (min-width: 768px) {
              width: 50%;
            }

            &:nth-child(1) {
              margin-right: 10px;

              @media screen and (min-width: 768px) {
                text-align: right;
              }
            }

            &:nth-child(2) {
              @media screen and (min-width: 768px) {
                margin-left: 10px;

                text-align: left;
              }
            }
          }
        }
      }
    }
  }
</style>



