<template>
  <section id="contact">
    <div class="wave-section5-content">
      <img class="logo" src="../assets/s5-contact.png" alt="selfpick" />
      <div class="content icons">
        <a href="https://www.facebook.com/selfpick/" target="_blank">
          <img src="../assets/01-facebook.png" alt="selfpick.cc" />
          <img src="../assets/01-facebook-h.png" alt="selfpick.cc" />
          <span>SELFPICK</span>
        </a>
        <a href="https://www.instagram.com/selfpick/" target="_blank">
          <img src="../assets/02-ig.png" alt="selfpick.cc" />
          <img src="../assets/02-ig-h.png" alt="selfpick.cc" />
          <span>SELFPICK</span>
        </a>
        <a href="mailto:ourselves@selfpick.cc" target="_blank">
          <img src="../assets/03-mail.png" alt="selfpick.cc" />
          <img src="../assets/03-mail-h.png" alt="selfpick.cc" />
          <span>SELFPICK.CC</span>
        </a>
        <a href="https://selftoken.co" target="_blank">
          <img src="../assets/04-s.png" alt="selfpick.cc" />
          <img src="../assets/04-s-h.png" alt="selfpick.cc" />
          <span>SELFTOKEN.CO</span>
        </a>
        <a href="https://jimlin94.github.io/2" target="_blank">
          <img src="../assets/05-doublesquare.png" alt="selfpick.cc" />
          <img src="../assets/05-doublesquare-h.png" alt="selfpick.cc" />
          <span>SELF BAR</span>
        </a>
      </div>
    </div>
    <div class="wave-wrapper wave-animation">
      <div class="wave-wrapper-inner bg-top">
        <div class="wave wave-top" />
      </div>
      <div class="wave-wrapper-inner bg-middle">
        <div class="wave wave-middle" />
      </div>
      <div class="wave-wrapper-inner bg-bottom">
        <div class="wave wave-bottom" />
      </div>
    </div>
  </section>
</template>
<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Work',
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.wave-section5-content', {
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style scoped lang="scss">
  @keyframes move-wave {
    0% {
      transform: translateX(0) translateZ(0) scaleY(1);
    }
    50% {
      transform: translateX(-25%) translateZ(0) scaleY(0.55);
    }
    100% {
      transform: translateX(-50%) translateZ(0) scaleY(1);
    }
  }

  @keyframes move-wave-mobile {
    100%  {
      background-position: -1920px bottom;
    }
  }

  section {
    min-height: 650px;

    @media screen and (min-width: 768px){
      min-height: auto;
    }
  }

  #contact {
    position: relative;
  }

  .wave-section5-content {
    height: 100vh;
    max-height: 750px;

    position: relative;
    z-index: 5;

    @media screen and (min-width: 768px) {
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .logo {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      z-index: 1;
    }

    .content {
      max-width: 80%;
      width: 100%;
      position: relative;
      margin: auto;
      padding-top: 20%;
      z-index: 2;

      @media screen and (min-width: 768px) {
        padding-top: 0%;
        display: flex;
        justify-content: space-around;
      }

      > a {
        width: 45%;
        height: auto;
        margin: 0 10px;
        padding: 20px;
        border-radius: calc(40vw / 4);
        display: inline-block;
        text-align: center;
        background-color: rgba(227, 175, 167, 0.6);
        text-decoration: unset;
        font-size: 0.8em;

        @media screen and (max-width: 768px) {
          margin: 0 5px 30px;

          &:nth-child(1) {
            margin: 10% auto;
            display: block;
          }
        }

        > * {
          margin: auto;
          display: block;
        }

        @media screen and (min-width: 768px) {
          width: 180px;
          height: 180px;
          border-radius: 60px;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          font-size: 1em;
        }

        img {
          // width: 50%;
          // height: 50%;

          @media screen and (min-width: 768px) {
            width: auto;
          }

          &:nth-child(2) {
            display: none;
          }
        }

        &:hover {
          img {
            &:nth-child(1) {
              display: none;
            }

            &:nth-child(2) {
              display: block;
            }
          }
        }
      }

      span {
        margin-top: 10px;
        color: #ffffff;
      }
    }
  }


  .wave-wrapper {
    overflow: hidden;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    transform: rotate(180deg);
  }

  .wave-wrapper-inner {
    position: absolute;
    width: 100%;
    overflow: hidden;
    height: 100%;
    bottom: 0px;
    background-image: linear-gradient(to bottom, rgb(229, 108, 65) 20%, rgb(207, 86, 71) 80%);
  }
  .bg-top {
    z-index: 3;
    opacity: 0.5;
  }
  .bg-middle {
    z-index: 2;
    opacity: 0.75;
  }
  .bg-bottom {
    z-index: 1;
  }
  .wave-top {
    background: url('../assets/wave-top.png');
    background-size: 960px 100px;

    @media screen and (min-width: 768px){
      background-size: 50% 100px;
    }
  }
  .wave-animation .wave-top {
    -webkit-animation:100s move-wave-mobile infinite linear;
    animation:100s move-wave-mobile infinite linear;
    will-change: animation;

    @media screen and (min-width: 768px){
      animation: move-wave 3s;
      -webkit-animation: move-wave 3s;
      -webkit-animation-delay: 1s;
      animation-delay: 1s;
    }
  }
  .wave-middle {
    background: url('../assets/wave-mid.png');
    background-size: 960px 120px;

    @media screen and (min-width: 768px){
      background-size: 50% 120px;
    }
  }
  .wave-animation .wave-middle {
    -webkit-animation:200s move-wave-mobile infinite linear;
    animation:200s move-wave-mobile infinite linear;
    will-change: animation;

    @media screen and (min-width: 768px){
      -webkit-animation: move-wave 10s linear infinite;
      animation: move-wave 10s linear infinite;
    }
  }
  .wave-bottom {
    background: url('../assets/wave-bot.png');
    background-size: 960px 100px;

    @media screen and (min-width: 768px) {
      background-size: 50% 100px;
    }
  }
  .wave-animation .wave-bottom {
    -webkit-animation:300s move-wave-mobile infinite linear;
    animation:300s move-wave-mobile infinite linear;
    will-change: animation;

    @media screen and (min-width: 768px){
      -webkit-animation: move-wave 15s linear infinite;
      animation: move-wave 15s linear infinite;
    }
  }
  .wave {
      position: absolute;
      left: 0;
      width: 200%;
      height: 100%;
      background-repeat: repeat no-repeat;
      background-position: 0 bottom;
      transform-origin: center bottom;
  }
</style>



