<template>
  <section id="work">
    <img class="logo" src="../assets/s4-work.png" alt="selfpick" />
    <a
      class="work-content"
      href="https://www.youtube.com/channel/UCiwXV-MzBk3nb1LOXa1tURA/featured"
      target="_blacnk"
    >
      <img class="logo layer1" src="../assets/s4-circle.png" alt="selfpick" />
      <img class="logo layer2" src="../assets/s4-youtube.png" alt="selfpick" />
    </a>
  </section>
</template>
<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Work',
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('.layer1', {
      duration: 1000,
      opacity: 0,
    });
    this.scrollReveal.reveal('.layer2', {
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style scoped lang="scss">
  #work {
    position: relative;
  }

  .logo {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    z-index: 1;

    &.layer1 {
      width: 60%;
      z-index: 2;

      @media screen and (min-width: 768px){
        width: auto;
        max-height: 80vh;
      }
    }

    &.layer2 {
      width: 30%;
      z-index: 3;

      @media screen and (min-width: 768px){
        width: auto;
        max-height: 40vh;
      }
    }
  }

</style>



