<template>
  <section id="landing">
    <div class="wave-content">
      <h2>{{ title }}</h2>
      <img class="logo" src="../assets/sp-logo.png" alt="selfpick" />
    </div>
    <div class="wave-wrapper wave-animation">
      <div class="wave-wrapper-inner bg-top">
        <div class="wave wave-top" />
      </div>
      <div class="wave-wrapper-inner bg-middle">
        <div class="wave wave-middle" />
      </div>
      <div class="wave-wrapper-inner bg-bottom">
        <div class="wave wave-bottom" />
      </div>
    </div>
  </section>
</template>

<script>
import scrollReveal from 'scrollreveal';

export default {
  name: 'Landing',
  data() {
    return {
      msg: 'section 1 Ya',
      title: 'We pick our own path',
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal('#landing h2', {
      origin: 'left',
      duration: 1000,
      opacity: 0,
    });
  },
};
</script>

<style lang="scss" scoped>
  @keyframes move-wave {
    0% {
      transform: translateX(0) translateZ(0) scaleY(1);
    }
    50% {
      transform: translateX(-25%) translateZ(0) scaleY(0.55);
    }
    100% {
      transform: translateX(-50%) translateZ(0) scaleY(1);
    }
  }

  @keyframes move-wave-mobile {
    100%  {
      background-position: -1920px bottom;
    }
  }

  section {
    max-height: 700px;
    position: relative;
    color: #fff;
    overflow: hidden;
  }

  #landing {
    height: 100vh;
    max-height: 700px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    h2 {
      position: relative;
      font-size: 20px;
      font-weight: 300;
      letter-spacing: 3px;
      text-transform: uppercase;
      z-index: 5;
    }

    .logo {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      padding: 5%;
      z-index: 4;
    }
  }

  .wave-wrapper {
    overflow: hidden;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: auto;
  }

  .wave-wrapper-inner {
    position: absolute;
    width: 100%;
    overflow: hidden;
    height: 100%;
    bottom: 0px;
    background-image: linear-gradient(to bottom, rgb(229, 108, 65) 20%, rgb(207, 86, 71) 80%);
  }
  .bg-top {
    z-index: 3;
    opacity: 0.5;
  }
  .bg-middle {
    z-index: 2;
    opacity: 0.75;
  }
  .bg-bottom {
    z-index: 1;
  }
  .wave-top {
    background: url('../assets/wave-top.png');
    background-size: 960px 100px;

    @media screen and (min-width: 768px){
      background-size: 50% 100px;
    }
  }
  .wave-animation .wave-top {
    -webkit-animation:100s move-wave-mobile infinite linear;
    animation:100s move-wave-mobile infinite linear;
    will-change: animation;

    @media screen and (min-width: 768px){
      animation: move-wave 3s;
      -webkit-animation: move-wave 3s;
      -webkit-animation-delay: 1s;
      animation-delay: 1s;
    }
  }
  .wave-middle {
    background: url('../assets/wave-mid.png');
    background-size: 960px 120px;

    @media screen and (min-width: 768px){
      background-size: 50% 120px;
    }
  }
  .wave-animation .wave-middle {
    -webkit-animation:200s move-wave-mobile infinite linear;
    animation:200s move-wave-mobile infinite linear;
    will-change: animation;

    @media screen and (min-width: 768px){
      -webkit-animation: move-wave 10s linear infinite;
      animation: move-wave 10s linear infinite;
    }
  }
  .wave-bottom {
    background: url('../assets/wave-bot.png');
    background-size: 960px 100px;

    @media screen and (min-width: 768px) {
      background-size: 50% 100px;
    }
  }
  .wave-animation .wave-bottom {
    -webkit-animation:300s move-wave-mobile infinite linear;
    animation:300s move-wave-mobile infinite linear;
    will-change: animation;

    @media screen and (min-width: 768px){
      -webkit-animation: move-wave 15s linear infinite;
      animation: move-wave 15s linear infinite;
    }
  }
  .wave {
      position: absolute;
      left: 0;
      width: 200%;
      height: 100%;
      background-repeat: repeat no-repeat;
      background-position: 0 bottom;
      transform-origin: center bottom;
  }
</style>



