<template>
  <div id="nav">
    <nav v-bind:class='{ "open-menu": isActive }'>
      <div class="nav-container">
        <div
          v-for="item in navItems"
          :key="item.id"
        >
          <a
            :href="item.domId"
            @click="isActive = false"
          >
            {{ item.name }}
          </a>
        </div>
      </div>
    </nav>
    <button
      class="menu-links hamburger hamburger--collapse"
      v-bind:class='{ "is-active": isActive }'
      type="button"
      v-on:click="toggleMenu">
      <span class="hamburger-box">
        <span class="hamburger-inner"></span>
      </span>
    </button>
  </div>
</template>

<script>
export default {
  name: 'Menu',
  data() {
    return {
      navItems: [
        { id: 1, name: 'Member', domId: '#member' },
        { id: 2, name: 'About', domId: '#about' },
        { id: 3, name: 'Work', domId: '#work' },
        { id: 4, name: 'Contact', domId: '#contact' },
      ],
      isActive: false,
    };
  },
  methods: {
    toggleMenu() {
      this.isActive = !this.isActive;
    },
  },
};
</script>

<style lang="scss" scoped>
  @import "../hamburgers.css";

  #nav {
    position: fixed;
    width: 100%;
    top: 0;
    right: 0;
    z-index: 100;
  }

  nav {
    position: absolute;
    top: -60px;
    width: 100%;
    opacity: 0;
    background-color: rgb(110, 49, 31);
    z-index: 6;
    transition: all 0.5s;

    @media screen and (min-width: 768px) {
      background-color: #1B32BB;
    }

    &.open-menu {
      top: 0;
      opacity: 1;
    }

    .nav-container {
      // width: 40%;
      height: 100vh;
      margin: auto;
      padding: 30% 0;
      display: flex;
      flex-direction: column;
      align-items: center;
      align-content: space-evenly;
      font-size: 20px;
      text-transform: uppercase;

      @media screen and (min-width: 768px) {
        width: 90%;
        height: 60px;
        padding: 0;
        flex-direction: row;
        align-items: center;
      }
    }

    div {
      flex: 1;
      // display: flex;
      align-content: center;
      align-items: center;
      justify-content: center;

      a {
        color: #fff;
        font-size: 2em;
        // cursor: pointer;
        outline: none;
        -webkit-user-select: none;
        text-decoration: none;

        &:focus {
          background: none;
        }

        @media screen and (min-width: 768px) {
          height: 60px;
          padding-top: 12px;
          font-size: 25px;
          flex-direction: row;
          align-items: center;
        }
      }
    }
  }

  .menu-links {
    position: absolute;
    // width: 30px;
    // height: 30px;
    top: 0;
    left: 0;
    // cursor: pointer;
    z-index: 7;

    @media screen and (min-width: 768px) {
      right: 0;
      left: auto;
    }

    .menu-bar {
      display: block;
      width: 30px;
      height: 3px;
      background-color: #fff;
      -webkit-border-radius: 3px;
      -moz-border-radius: 3px;
      border-radius: 3px;
      margin: 0 auto 8px;
      transition: background-color 0.2s;
    }
  }
</style>



